@import "../../global.scss";

.divItem {
    width: 400px;
    height: 200px;
    margin: 6px;
    border-radius: 10px;
    border: 1px solid $StrikingAndSimpleOne;

    text-align: center;

    display: flex;
    justify-content: center;
    align-items: center;
    color: white;

    position: relative;

    transition: all 0.5s ease;
    cursor: pointer;

    @include mobile {
        width: 150px;
        height: 120px;
    }

    img {
        width: 100%;
        height: 100%;
        z-index: 1;
    }

    h3 {
        position: absolute;
        font-size: 20px;
    }

    &:hover {
        background-color: $mainColor;
        img {
            z-index: 0;
            opacity: 0.2;
        }
    }
}
